.radiobutton {

    background: transparent;
    color: #404040;

    width: 40px;
    height: 50px;

    border: none;
    outline: none;

    -webkit-transition: all 0.5s;

    -o-transition: all 0.5s;

    transition: all 0.5s;
}

.radiobutton:hover {

    color: #eeeeee;
}


